}

.lang-switcher__dropdown {
  scrollbar-width: thin;
  scrollbar-color: #f4970f transparent;
  text-align: center;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  background-color: #000;
  border-radius: 4px;
  max-width: 60px;
  max-height: 111px;
  margin: 0;
  padding: 0;
  list-style: none;
  transition: opacity .3s, transform .3s, visibility .3s;
  position: absolute;
  top: 100%;
  left: -5px;
  overflow-y: auto;
  transform: translateY(-10px);
}

.lang-switcher__dropdown li:last-child {
  border-bottom: none;
}

.lang-switcher__dropdown a {
  color: #f4970f;
  padding: 10px;
  font-size: 16px;
  text-decoration: none;
  transition: background-color .2s, color .2s;
  display: block;
}

.lang-switcher__dropdown a:hover {
  color: #1a1a1a;
  background-color: #00c4cc;
}

.lang-switcher__dropdown li:first-child a {
  color: #fff;
}

.lang-switcher.is-open .lang-switcher__arrow {
  transform: rotate(180deg);
}

.lang-switcher.is-open .lang-switcher__dropdown {
  opacity: 1;
  visibility: visible;
  transform: translateY(10px);
}

